@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: 'Spartan', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    height: 35vh;
    width: 100%;
}

.title-container {
    display: flex;
    justify-content: center;
    overflow-wrap: break-word;
}

.title {
    font-size: 5vw;
    text-transform: uppercase;
}

.inputs {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.search {
    width: 35%;
}

.select {
    width: 35%;
}

@media (max-width: 900px) {
    .header {
        justify-content: space-evenly;
        height: 35vh;
    }
    .title {
        font-size: 10vw;
    }
}

.css-vydhe7-MuiFormLabel-root-MuiInputLabel-root.Mui-focused {
    color: black !important;
}
.meanings {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: thin;
    height: 55vh;
    border: 10px solid rgb(105, 105, 105);
    border-radius: 10px;
    padding: 10px 20px;
    overflow-x: hidden;
}

.subTitle {
    font-size: 3vh;
}

.singleMeaningLight {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 10px 0;
    background-color: #282C34;
    color: white;
}
.singleMeaningDark {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 10px 0;
    background-color: white;
    color: #282C34;
}

.singleMeaning hr {
    background-color: black;
}

@media (max-width: 900px) {
    .meanings {
        height: 53vh;
        overflow-x: hidden;
        overflow-y: scroll;
    }

    .subTitle {
        font-size: 2vh;
    }
}

.audio {
    background-color: white;
    margin: 10px 0;
}
.AppDarkMode {
    height: 100vh;
    background: #282C34;
    color: white;
    transition: all 0.3s linear;
}
.AppLightMode {
    height: 100vh;
    background: white;
    color: #282C34;
    transition: all 0.3s linear;
}

.Container {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.themeSwitch {
    position: absolute;
    top: 0px;
    right: 15px;
    padding-top: 10px;
}

.visit-me-link {
    position: absolute;
    left: 0px;
    bottom: 40px;
    font-size: 15px;
    font-weight: 600;
    font-family: 'Spartan', sans-serif !important;
    background-color: black;
    color: white;
    text-decoration: none;
    padding: 1rem;
    text-transform: uppercase;
    transition: all .2s ease-in;
}
  
.visit-me-link:hover {
    padding-left: 30px;
}

@media (min-width: 320px) and (max-width: 480px) {
	.visit-me {
	    display: none !important;
	}
}
